.stamp {
  position: relative;
  width: 400px;
  border-radius: 10px;
  padding: 18px;
  overflow: hidden;
  -webkit-filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
  background: radial-gradient(transparent 8px, #fff 4px, #fff);
  background-size: 400px 400px;
  background-position: 200px;
}
.stamp::before, .stamp::after {
    content: "";
    position: absolute;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background: #fff;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}
.stamp::before {
    top: 0;
    margin-top: -16px;
}
.stamp::after {
    top: 100%;
    margin-top: 16px;
}
.stamp-apart {
  width: 400px;
  -webkit-filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
}
.stamp-inner {
  padding: 18px;
  background: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.stamp-top {
  border-radius: 10px 10px 0 0;
  padding-bottom: 0;
}
.stamp-bottom {
  border-radius: 0 0 10px 10px;
  padding-top: 0;
}
.stamp-center {
  width: 400px;
  height: 16px;
  overflow: hidden;
  background: radial-gradient(transparent 8px, #fff 1px);
  padding: 14px;
  background-size: 400px 400px;
  background-position: 200px;
}
.stamp-center::before {
    content: '';
    display: block;
    margin: 0 6px;
    border-top: 1px dashed #e4e4e4;
}
